<!--服务质量过程监控-故障报修-->
<template>
  <div class="fault">
    <!-- 故障报修类型占比 -->
    <div
      class="fault_left charts_main"
      v-loading="!resGzbxCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">故障报修类型占比</div>
      <div class="pie_charts">
        <bda-pie-charts
          v-if="resGzbxCharts"
          :pieChartsData="gzbxChartsData"
          :roseType="false"
          :radius="['45%', '75%']"
          :center="['29%', 'center']"
          :labelType="'emphasis'"
          :legendShow="true"
        ></bda-pie-charts>
      </div>
    </div>
    <!-- 高压故障类型占比 -->
    <div class="fault_center">
      <div
        class="high_fault charts_main"
        v-loading="!resGygzCharts"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <div class="title">高压故障类型占比</div>
        <div class="pie_charts">
          <bda-pie-charts
            v-if="resGygzCharts"
            :pieChartsData="gygzChartsData"
            :roseType="false"
            :radius="['0%', '80%']"
            :center="['15%', 'center']"
            :labelType="'none'"
            :legendShow="true"
            :legendRight="70"
            :legendTop="10"
          ></bda-pie-charts>
        </div>
      </div>
      <!-- 低压故障类型占比 -->
      <div
        class="low_fault charts_main"
        v-loading="!resDygzCharts"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <div class="title">低压故障类型占比</div>

        <div class="pie_charts">
          <bda-pie-charts
            class="pieCharts"
            v-if="resDygzCharts"
            :pieChartsData="dygzChartsData"
            :roseType="false"
            :radius="['0%', '80%']"
            :center="['15%', 'center']"
            :labelType="'none'"
            :legendShow="true"
            :legendRight="70"
            :legendTop="10"
          ></bda-pie-charts>
        </div>
      </div>
    </div>
    <!-- 各供电单位超时限处理完成量 -->

    <div
      class="fault_right charts_main"
      v-loading="!cswcLstatus"
      v-if="cswcLstatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">各供电单位超时限处理完成量</div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="cswcLstatus"
          :chartsConfig="cswcLChartsConfig"
          :barType="cswcRbarType"
          :barWidth="6"
        ></bda-3d-barCharts>
      </div>
    </div>

    <div
      class="fault_right charts_main"
      v-loading="!cswcRstatus"
      v-if="cswcRstatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">各供电单位超时限处理完成量</div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="cswcRstatus"
          :chartsConfig="cswcRChartsConfig"
          :barType="cswcRbarType"
          :barWidth="4"
        ></bda-3d-barCharts>
      </div>
    </div>
    <!-- <div
          class="fault_right_right"
          v-loading="!cswcRstatus"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <bda-3d-barCharts
            v-if="cswcRstatus"
            :chartsConfig="cswcRChartsConfig"
            :barType="cswcRbarType"
            :barWidth="2"
          ></bda-3d-barCharts>
          <div class="noData" v-if="cswcRChartsConfig.dimension.length===0">
            <span>暂无相关数据信息统计！</span>
          </div>
    </div>-->
  </div>
</template>

<script>
// import pieCharts from "@/widgets/bda-3d-pieCharts/bda-3d-pieCharts";
import bda3dBarCharts from '@/widgets/bda-3d-barCharts/bda-3d-barCharts';
import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts'

import {
  dataChange,
  dataFilter,
  translateFilter
} from '../../../commonJS/dataChanges'
import config from '../../../base/mixin/service-quality-process';

export default {
  name: "fault",
  mixins: [config],
  components: {
    bdaPieCharts,
    bda3dBarCharts
  },
  data() {
    return {
      // 饼图数据(故障报修)是否返回
      resGzbxCharts: false,
      // 饼图数据(高压故障)是否返回
      resGygzCharts: false,
      // 饼图数据(低压故障)是否返回
      resDygzCharts: false,
      //  // 柱状图数据(超时完成量左)是否返回
      //  resCswcLCharts: false,
      //  // 柱状图数据(超时完成量右)是否返回
      //  resCswcRCharts: false,
      // 饼图数据(故障报修)
      gzbxChartsData: {},
      // 饼图数据(高压故障)
      gygzChartsData: {},
      // 饼图数据(低压故障)
      dygzChartsData: {},
      //  // 柱状图数据(超时完成量左)
      cswcLChartsConfig: {},
      cswcLstatus: false,
      cswcLbarType: 'stack',
      //  // 柱状图数据(超时完成量右)
      cswcRChartsConfig: {},
      cswcRbarType: 'stack',
      cswcRstatus: false
    }
  },
  watch: {},
  mounted() {
  },
  created() {
    this.getData();

  },
  methods: {
    //故障报修
    gzbxFilter(data) {
      let pieData = {
        dataMap: [],
        color: []
      }
      pieData.color = ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF',]
      if (data && data.length !== 0) {
        pieData.dataMap = data.map(e => {
          return {
            value: e.ccsum,
            name: e.name
          }
        })
      }
      this.resGzbxCharts = false;
      this.gzbxChartsData = pieData
      // window.console.log(pieData, ' this.pieData');

      this.$nextTick(() => {
        this.resGzbxCharts = true;
      })
    },
    //高压故障
    gygzFilter(data) {
      let pieData = {
        dataMap: [],
        color: []
      }
      pieData.color = ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF',]
      if (data && data.length !== 0) {
        pieData.dataMap = data.map(e => {
          return {
            value: e.ccsum,
            name: e.name
          }
        })
      }
      this.resGygzCharts = false;
      this.gygzChartsData = pieData
      window.console.log(pieData, ' this.pieData');

      this.$nextTick(() => {
        this.resGygzCharts = true;
      })
    },
    //低压故障
    dygzFilter(data) {
      let pieData = {
        dataMap: [],
        color: []
      }
      let sortName = (property) => {
        return function (a, b) {
          const val1 = a[property];
          const val2 = b[property];
          return val1.length - val2.length;
        }
      }
      pieData.color = ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF',]
      if (data && data.length !== 0) {
        pieData.dataMap = data.sort(sortName('name')).map(e => {
          return {
            value: e.ccsum,
            name: e.name
          }
        })
      }
      this.resDygzCharts = false;
      this.dygzChartsData = pieData

      window.console.log(this.dygzChartsData, ' this.dygzChartsData');
      this.$nextTick(() => {
        this.resDygzCharts = true;
      })
    },
    // 超时完成
    getCsData() {
      let $this = this;
      let type = this.$parent.orgType
      this.getMalfunctionForOutTime({
        orgType: type,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        if (res && res.length !== 0) {
          let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
          let _info = dataChange(data, 'orgno', 'orgName');
          //   window.console.log('_info', _info);
          let colorMap = {
            '城市': ['#6AFFA2', '#00BDDE'],
            '农村': ['#005FFF', '#009DFF'],
            // '边远地区': ['#00ABFF', '#00E5FF'],
          }
          let nData = dataFilter(_info, {
            name: 'urbanruralflag',
            translate: {
              '1': '城市',
              '2': '农村',
              // '3': '边远地区'
            },
            valueField: 'sum'
          }, 'orgName');
          nData.colorMap = colorMap;
          if (type === '1') {
            $this.cswcLChartsConfig = nData;
            $this.cswcRstatus = false;
            $this.$nextTick(() => {
              $this.cswcLstatus = true;
            })
          } else {
            $this.cswcRChartsConfig = nData;
            $this.cswcLstatus = false;
            $this.$nextTick(() => {
              $this.cswcRstatus = true;
            })
          }
        } else {
          if (type === '1') {
            $this.cswcLstatus = true;
          } else {
            $this.cswcRstatus = true;
          }
        }
      })
        .catch(() => {
          if (type === '1') {
            $this.cswcLstatus = true;
          } else {
            $this.cswcRstatus = true;
          }
        })
    },
    getPieData() {
      //故障报修
      this.getMalfunctionForType({
        orgType: this.$parent.orgType,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        //  window.console.log('故障报修', res)
        this.gzbxFilter(res)
      }).catch(() => {
        this.resGzbxCharts = true;
      })
      //  高压故障
      this.getMalfunctionForHighType({
        orgType: this.$parent.orgType,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        //  window.console.log('高压故障', res)
        this.gygzFilter(res)
      }).catch(() => {
        this.resGygzCharts = true;
      });

      //低压故障
      this.getMalfunctionForLowType({
        orgType: this.$parent.orgType,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        //  window.console.log('低压故障', res)
        this.dygzFilter(res)
      }).catch(() => {
        this.resDygzCharts = true;
      })
    },
    getData() {
      this.getCsData();
      this.getPieData();
    }

  }

}
</script>

<style lang="scss" scoped>
.fault {
  display: flex;
  //  justify-content: space-around;
  //  margin-top: -10px;
  height: 100%;
  .noData {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 14px;
    }
  }

  .title {
    width: 100%;
    height: 16%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 20px;
    color: #00e7ff;
  }

  .charts_main {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .fault_left {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    height: 100%;
    float: left;
    position: relative;
    .pie_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
  .fault_center {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .title {
      width: 100%;
      height: 30%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      font-size: 20px;
      color: #00e7ff;
    }
    .pie_charts {
      width: 100%;
      height: calc(100% - 30%);
    }
    .high_fault {
      width: 100%;
      height: 55%;
    }
    .low_fault {
      width: 100%;
      height: 50%;
      .title {
        height: 15%;
      }
      .pie_charts {
        width: 100%;
        height: calc(100% - 20%);
      }
    }
  }

  .fault_right {
    width: 56%;
    float: left;
    height: 100%;
    position: relative;
    background: url("../../../assets/img/监控框7.png") no-repeat top center;
    background-size: 100% 100%;

    .bar_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
}
</style>